.card {
  position: relative;
  width: 100px;
  margin: 3%;
  height: 150px;
  border-radius: 4px;
  text-align: center;
  box-shadow: -4px 3px 3px #aba8a840;
  overflow: hidden;
  -webkit-transition: 0.5s;
  transition: 0.5s;
  display: flex;
  justify-content: center;
  align-items: center;
  background: var(--color3); }
  .card svg {
    position: relative;
    width: 100%;
    height: 100%;
    z-index: 10000; }
    .card svg circle {
      width: 100%;
      height: 100%;
      fill: none;
      stroke: #d6d6d6;
      stroke-linecap: round;
      stroke-width: 5;
      -webkit-transform: translate(7px, 7px);
              transform: translate(7px, 7px); }
      .card svg circle:nth-child(2) {
        stroke-dasharray: 229; }
  .card:hover {
    -webkit-transform: translateY(-10px);
            transform: translateY(-10px);
    box-shadow: 0 15px 35px var(--card-shadow); }
    .card:hover .card-text {
      color: var(--contrary); }
    .card:hover .number h2 {
      color: var(--contrary);
      font-size: 1.8rem; }
      .card:hover .number h2 span {
        color: var(--contrary); }
  .card:before {
    height: 100%;
    content: "";
    position: absolute;
    left: 0;
    top: 0;
    background: var(--card);
    pointer-events: none;
    z-index: 1;
    width: 50%; }

.percent {
