.card-text {
  position: relative;
  color: #777;
  margin-top: 20px;
  font-weight: 700;
  font-size: 18px;
  letter-spacing: 1px;
  z-index: 999;
  text-transform: uppercase;
  -webkit-transition: 0.5s;
  transition: 0.5s; }

.card:nth-child(1) svg circle:nth-child(2) {
  stroke-dashoffset: calc(440 - (440 * 77) / 100);
  -webkit-animation-name: cargar;
          animation-name: cargar;
  -webkit-animation-duration: 4s;
          animation-duration: 4s;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
  stroke: var(--strong); }

.card:nth-child(2) svg circle:nth-child(2) {
  stroke: var(--blue);
  stroke-dashoffset: calc(440 - (440 * 90) / 100);
  -webkit-animation-name: cargar;
          animation-name: cargar;
  -webkit-animation-duration: 4s;
          animation-duration: 4s;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards; }

.card:nth-child(3) svg circle:nth-child(2) {
  stroke-dashoffset: calc(440 - (440 * 87) / 100);
  stroke: #f36e87;
  -webkit-animation-name: cargar;
          animation-name: cargar;
  -webkit-animation-duration: 4s;
          animation-duration: 4s;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards; }

.card:nth-child(4) svg circle:nth-child(2) {
  stroke-dashoffset: calc(440 - (440 * 83) / 100);
  -webkit-animation-name: cargar;
          animation-name: cargar;
  -webkit-animation-duration: 4s;
          animation-duration: 4s;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
  stroke: #d26ef3; }

@-webkit-keyframes cargar {
  from {
    stroke-dashoffset: 229; } }

@keyframes cargar {
  from {
    stroke-dashoffset: 229; } }

